	module.exports = {"data":{"postsJson":{"title":"Introducción a Juan","questionsTitle":null,"display":"none","bookName":"Juan","ref":null,"html":"<h6>Para que creas</h6><p>Jesús se reveló como el Hijo de Dios, lleno de gracia y verdad</p><h6>Cómo leer Juan</h6><p>En los días de Jesús, al igual que en la actualidad, existían muchos sistemas de creencias que competían y trataban de conectarnos con una realidad espiritual mas allá de nosotros. Una de las religiones con más seguidores ofrece un dios de poder y de venganza; otra adora a un dios que guarda silencio y es indiferente al sufrimiento de la gente; y otra ofrece a un dios misterioso e incomprensible que absorbe a la humanidad en un gran océano cósmico integrador. Algunas personas adoran a los «dioses» de la riqueza, la fama y el entretenimiento. Solo una fe, el cristianismo, adora a un ser increíble y personal, quien es conocido principalmente por su amor sacrificado. Este libro retrata a ese Dios único revelado en la persona de Jesucristo: Dios hecho carne.<br />   Al examinar este relato de la vida de Jesús te verás confrontado con algunas afirmaciones sorprendentes acerca de su persona: Quién es Él y qué vino a hacer. Juan nos asegura que escogió una pequeña porción de las muchas obras que Jesucristo realizó para ayudarnos a entender quién es Él. Juan solo registra siete milagros, llegando a un clímax en la resurrección de Jesús. Para él, estas señales son una prueba indiscutible que Jesús es el Hijo de Dios. Es más, afirma que si se escribieran cada una de las poderosas obras que hizo Jesús «el mundo entero no podría contener los libros que se escribirían.» (Jua 21:25). Sin embargo, ¿por qué seleccionó estos episodios? Fue para provocar una respuesta de fe en el corazón «para que ustedes continúen creyendo que Jesús es el Mesías, el Hijo de Dios, y para que, al creer en él, tengan vida por el poder de su nombre.» (Jua 20:31)<br />   Observa las características únicas del Evangelio de Juan. Las imágenes de luz y de vida que describen la actividad divina en el mundo. Juan incluye también varios sermones de Cristo no registrados en otros evangelios (3:10-21; 5:19-47; 9:41-10:5; 10:7-18; 10:25-30; 13:7; 13:8; 13:10; 13:12-20; 13:21; 13:26; 13:27; 13:31-35; 13:36; 13:38-14:4; 14:6-7; 14:9-21; 14:23-16; 16:19-28; 16:31-33; 17:1-26). Vemos que Juan resalta las veces que Jesús declara «Yo Soy». Jesús proclama que Él es el Mesías, el pan de vida del cielo, el enviado por Dios, la luz de este mundo, la puerta, el Buen Pastor, el Hijo de Dios, la resurrección, la vida, el camino, la verdad, la vid verdadera y el Rey de los Judíos. ¡Estas afirmaciones deberían proporcionarte evidencia más que suficiente para creer!</p><h6>¿Quién escribió este libro?</h6><p>El apóstol Juan, uno de los primeros doce discípulos de Jesús.</p><h6>¿Cuándo y dónde se escribió?</h6><p>Entre el año 80 y el 95 d. C., aunque algunos eruditos sostienen que pudo haberlo escrito entre el año 50 y el 70 d. C. Juan se hallaba probablemente en Éfeso, ciudad situada en la actual Turquía.</p><h6>¿A quién se escribió?</h6><p>A los seguidores de Jesús que no eran judíos, específicamente a aquellos que tenían dificultades con la filosofía griega que enseñaba que la salvación se obtiene a través de un conocimiento especial y que Jesús era divino, pero no auténticamente humano. Juan insiste en que la salvación se recibe por la fe en el Hijo de Dios, Jesucristo, que se hizo carne.</p><h6>¿Para qué se escribió?</h6><p>El propio Juan lo explica (Jua 20:31). Con un fin evangelístico, Juan desea que todos tengan vida eterna mediante el conocimiento de Jesucristo.</p><h6>Perspectiva de SourceView</h6><p>En Juan, tres de los doce discípulos no presentan diálogos: Mateo-Leví, Santiago hijo de Alfeo y Simón el zelote —ver la introducción de Mateo—. Los otros nueve tienen un participación mucho mayor que en los otros tres evangelios. El discípulo con el parlamento más pequeño es Santiago, ¡su propio hermano! ¿No es acaso algo típico de los hermanos?<br />   Observa el número de palabras que se le atribuye a cada discípulo en los distintos evangelios:</p><br /><table style='font-size: 15px; max-width: 500px; margin: 0 auto'> <tr> <th></th> <th style='width: 12%'>Mat</th> <th style='width: 12%'>Mar</th> <th style='width: 12%'>Luc</th> <th style='width: 12%'>Jua</th> </tr><tr> <th>Simón Pedro</th> <th>147</th> <th>115</th> <th>140</th> <th>112</th> </tr><tr> <th>Andrés</th> <th>-</th> <th>15</th> <th>15</th> <th>29</th> </tr><tr> <th>Juan el Discípulo</th> <th>4</th> <th>96</th> <th>55</th> <th>10</th> </tr><tr> <th>Santiago hijo de Zebedeo</th> <th>4</th> <th>73</th> <th>27</th> <th>4</th> </tr><tr> <th>Felipe el Discípulo</th> <th>-</th> <th>-</th> <th>-</th> <th>47</th> </tr><tr> <th>Natanael-Bartolomé</th> <th>-</th> <th>-</th> <th>-</th> <th>28</th> </tr><tr> <th>Tomás</th> <th>-</th> <th>-</th> <th>-</th> <th>61</th> </tr><tr> <th>Judas-Tadeo</th> <th>-</th> <th>-</th> <th>-</th> <th>16</th> </tr><tr> <th>Judas Iscariote</th> <th>30</th> <th>17</th> <th>-</th> <th>19</th> </tr></table><br /><p>   En las narraciones de los cuatro evangelios, Simón Pedro tiene el rol protagónico dentro de los discípulos. El segundo lugar en Mateo lo tiene Judas Iscariote; Juan el Discípulo ocupa este puesto en Marcos y Lucas; pero Tomás tiene este honor en el Evangelio de Juan. A la luz del propósito expreso del libro (Jua 20:31), los problemas que Tomás tuvo para creer que Jesús resucitó de los muertos son una clave para que Juan destaque la importancia de la fe y fidelidad del testimonio de aquellos que fueron los primeros testigos oculares de la resurrección de Jesús. Muchas veces somos muy duros con Tomás, catalogándolo de ‘incrédulo’ (20:25) cuando en realidad es un ejemplo de un compromiso radical. Él fue el primero en expresar su disposición de entregar su vida para el Señor. Su declaración, «Vamos nosotros también y moriremos con Jesús.» (11:16) puede no sonar muy optimista o esperanzadora, ¡pero sin duda expresa un corazón dispuesto y comprometido! ¿Hasta qué punto llegarás en tu caminar con Jesús?<br />","path":"/339","Question1":null,"Question2":null,"Question3":null,"Question4":null,"prevPath":"/338","nextPath":"/340"}},"pathContext":{}}

